body{
    background-color: black;
    margin: 20px;
}

.show-title{
    color: white;
    font-size: 18px;
    font-weight: 200;
    margin-right: 20px;
}

.text {
    color:white;
}
  

  .image:focus {
    outline-color: white;
    border-color:white;
    background-color: white;

  }

  .image:active {
    outline-color: green;
    border-color: green;
    background-color: green;
  }
  

  .hide {
    visibility: hidden;
  }
  
  .show {
    visibility: visible;
  }

  .carousel-main {
    display: flex;
    flex-direction: column;
  }
  
  .category-title {
    color: white;
    padding-left: 25px;
}

  .carousel-content {
    display: flex;
    margin: 0 15px;
    overflow-x: hidden;
    scroll-behavior: smooth;

  }
  
  .image {
    margin-right: 20px;
    width: 300px;
    height: 150px;
    border-style:solid;
    background-size: 300px 150px;

  }
  
  .carousel-content > :last-child {
    margin-right: 0;
  }
  
  .carousel-navigation {
    justify-content: center;
    align-items: center;
  }

  .modal {
    position: fixed; 
    z-index: 1; 
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
  }
  
  .modal-content {
    background-color: black;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 50%;
  }
  
  .close {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: green;
    text-decoration: none;
    cursor: pointer;
  }
  
  .image-center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 150px;
  }

  .modal-text{
    text-align: left;
    padding-top: 20px;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
  }

  .sub-text{
    text-align: left;
    padding-top: 20px;
    font-size: 15px;
  }

  .text-small{
    text-align: left;
    font-size: 14px;
  }

  li {
    display: inline-block !important;
    list-style: none;
    margin: 0 auto;
    height: 14px;
    }
      ul li::after {
        content: " | ";
        margin: 0 10px;
      }
      ul li:last-child:after {
        content: '';
        margin: 0 10px;
      }
      ul{
        padding: 0;
        margin: 0;
      }
